import { require } from "../require"
/**
 * 获取推荐歌单 
 */
function playList(context, params = {}) {
    let url = "/personalized";
    return require(url, params, context);
}

/**
 * 更新歌单类型
 */
function tab(context, params = {}) {
    let url = "/top/playlist"
    return require(url, params, context);
}

/** 
*  高质量歌单
*/
function highquality(context, params = {}) {
    let url = "/top/playlist/highquality"
    return require(url, params, context);
}
/**
 * 获取新碟
 */
function disc(context, params = {}) {
    let url = "/top/album";
    return require(url, params, context);
}

/**
 * 获取独家放送
 */
function unipue(context, params = {}) {
    let url = "/personalized/privatecontent";
    return require(url, params, context);
}

/**
 * 获取歌单中歌曲的所有 id
 */
function songAllid(context, params = {}) {
    let url = "/playlist/detail";
    return require(url, params, context);

}
/**
 * 获取歌曲的信息（部分）
 */
function songDetail(context, params = {}) {
    let url = "/song/detail";
    return require(url, params, context);
}

/**
 * 获取歌单收藏者
 */
function subscribers(context, params = {}) {
    let url = "/playlist/subscribers";
    return require(url, params, context);
}
/**
 * 所有榜单
 */
function toplist(context, params = {}) {
    let url = "/toplist";
    return require(url, params, context);
}
/**
 * 获取音频
 */
function songUrl(context, params = {}) {
    let url ="song/url";
    return require(url, params, context);
}
/**
 * 获取歌词
 */
function lyric(context, params = {}) {
    let url = "/lyric";
    return require(url, params, context);
}
export {
    playList,
    tab,
    disc,
    unipue,
    songAllid,
    songDetail,
    subscribers,
    highquality,
    toplist,
    songUrl,
    lyric,
}