<template>
    <div class="song">
        <div class="music-contianer">

        </div>
    </div>
</template>
<script>
import { lyric, songUrl } from "../network/song/index.js";
import tools from "../tools/formatData.js";
export default {
  name: "song",
  components: {},
  data() {
    return {
      id: this.$route.query.id,
      url: "",
      cover: this.$route.query.cover,
      // 歌词处理后
      parseLyric: [],
    };
  },
  methods: {
    // 获取音频
    getsongUrl() {
      songUrl(this, { id: this.id })
        .then(res => {
          this.url = res.data[0].url;
        })
        .catch(error => {
          console.log(error);
        });
    },
    // 获取歌词
    getlyric() {
      lyric(this, { id: this.id })
        .then(res => {
         this.parseLyric = tools._parseLyric(res.lrc.lyric)
        })
        .catch(error => {
          console.log(error);
        });
    },
  },
  created() {
    this.getsongUrl();
    this.getlyric();
  },
};
</script>
<style lang="scss">
@import "../assets/scss/music.scss";
.song {
  .music-container {
  }
}
</style>